.module-announcement {
    background-size: cover;

    &__close {
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
    }

    .module__content {
        p:last-child {
            margin-bottom: 0;
        }
    }
}