/*--------------------------------------------------------------
# Modules
--------------------------------------------------------------*/
.ftf-module {
  position: relative;
}

/*--------------------------------------------------------------
- Announcement Module
--------------------------------------------------------------*/
.module-announcement {
  background-size: cover;
}
.module-announcement__close {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
.module-announcement .module__content p:last-child {
  margin-bottom: 0;
}